import React from 'react';
import { Box, Text, Grommet, Grid, Button, Image } from 'grommet';
import { Link } from 'react-router-dom';

import './Treasuremap.css';

function Treasuremap() {
  return (
    <Grommet>
      <Grid
        rows={['auto']}
        columns={['1/3', '2/3']}
        gap="small"
        areas={[
          { name: 'box1', start: [0, 0], end: [0, 0] },
          { name: 'box2', start: [1, 0], end: [1, 0] },
        ]}
        margin={{ top: '20px' }}
      >
        <Box gridArea="box1" background="white" pad="medium" align="center" justify="center">
          <Image
            src={require('./coal_transition.png')}
            style={{ width: '100%', maxWidth: '200px', height: 'auto' }} // Make the image responsive
          />
        </Box>
        <Box gridArea="box2" background="white" pad="medium" align="left" justify="center">
          <Text
            size= "large" // Increase the font size for larger screens
            weight="1000" // Adjust the font weight if needed
            margin={{ bottom: '5px' }}
            style={{ fontFamily: 'Calibri', color: '#000000' }}
            textAlign="left"
          >
            Coal to Clean Energy Transition
          </Text>
          <Text
            size="small"
            weight="500"
            margin={{ bottom: '0px' }}
            style={{ fontFamily: 'Calibri', color: '#303030' }}
            textAlign="left"
          >
            Market Cap: 40 Billion USD
          </Text>
          <Text
            size="small"
            weight="500"
            margin={{ bottom: '0px' }}
            style={{ fontFamily: 'Calibri', color: '#303030' }}
            textAlign="left"
          >
            Study Case : Indonesia
          </Text>
          <Text
            size="small"
            weight="500"
            margin={{ bottom: '0px' }}
            style={{ fontFamily: 'Calibri', color: '#303030' }}
            textAlign="left"
          >
            Company Type : Tech Company
          </Text>
          <Text
            size="small"
            weight="500"
            margin={{ bottom: '0px' }}
            style={{ fontFamily: 'Calibri', color: '#303030' }}
            textAlign="left"
          >
            Sector: Renewable Energy
          </Text>
          <Link to="/Coaltransition" style={{ marginRight: '100%', textAlign: 'left', marginLeft: '0%', width: '200px' }}>
            <Button
              style={{
                textDecoration: 'none',
                width: '100%',
                marginTop: '0px',
                borderRadius: '5%',
              }}
              size="small"
              primary
              color="#000000"
              label={
                <Text
                  size={{ xs: '14px', sm: '16px', md: '14px', lg: '16px', xl: '16px' }}
                  weight="550"
                  style={{ fontFamily: 'Calibri' }}
                  color="#ffffff"
                >
                  Visit Documentation
                </Text>
              }
            />
          </Link>
        </Box>
      </Grid>
    </Grommet>
  );
}

export default Treasuremap;
