import React from 'react';
import { Grommet, Header, Nav, Anchor, Main } from 'grommet';
import { Link, Route, Routes, Navigate } from 'react-router-dom';

import Treasuremap from './Treasuremap';
import About from './About';
import Coaltransition from './Treasuremap/Coaltransition';

const theme = {
  global: {
    colors: {
      brand: '#ffffff',
    },
  },
};

const Navbar = () => {
  return (
    <Grommet theme={theme} full>
      <Header background="brand" pad="small" elevation="small" position="fixed" width="100%">
        <Nav direction="row">
          <Link to="/About" style={{ textDecoration: 'none' }}>
            <Anchor color="black" size="large" style={{ fontSize: '17px', fontFamily: 'Calibri', fontWeight: 'normal' }} label="About" />
          </Link>
          <Link to="/Treasuremap" style={{ textDecoration: 'none' }}>
            <Anchor color="black" size="large" style={{ fontSize: '17px', fontFamily: 'Calibri', fontWeight: 'normal' }} label="Treasure Map" />
          </Link>
        </Nav>
      </Header>
      <Main>
        <Routes>
          <Route path="/" element={<Navigate to="/about" replace />} />
          <Route path="/About" element={<About />} />
          <Route path="/Treasuremap" element={<Treasuremap />} />
          <Route path="/Coaltransition" element={<Coaltransition />} />
        </Routes>
      </Main>
    </Grommet>
  );
};

export default Navbar;
